import * as React from 'react';
import * as ReactDOM from 'react-dom';

const accept: string = [
  `image/*`,
  `.pdf`,
  `.md`,
  `.doc`,
  `.docx`,
  `application/msword`,
  `application/vnd.openxmlformats-officedocument.wordprocessingml.document`,
].join(',');

export interface FileInputProps extends React.ComponentPropsWithoutRef<'input'> {
  name?: string;
}

export const FileInput = React.forwardRef<HTMLInputElement, FileInputProps>(({ name = 'files', ...props }, ref) => {
  return ReactDOM.createPortal(
    <input name={name} accept={accept} {...props} type="file" style={{ ...props.style, display: 'none' }} ref={ref} />,
    document.body
  );
});
